import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule, BaseRequestOptions, RequestOptions } from '@angular/http';
import { RouterModule } from '@angular/router';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { ContactComponent } from './components/contact/contact.component';
import { NavMenuComponent } from './nav-menu/nav-menu.component';

import { ToastrModule } from 'ngx-toastr';
import { CommonModule, LocationStrategy, HashLocationStrategy, APP_BASE_HREF, Location } from '@angular/common';
import { ContactService } from './_services/index';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { InputTextModule, DataTableModule, ButtonModule, DialogModule } from 'primeng/primeng';

class AppBaseRequestOptions extends BaseRequestOptions {
    Headers: Headers = new Headers();
    constructor() {
        super();
        this.headers.append('Content-Type', 'application/json');
        this.body = '';
    }
}


@NgModule({
    declarations: [
    AppComponent,
    ContactComponent,
    NavMenuComponent
  ],
  imports: [
      BrowserModule,
      AppRoutingModule,
      CommonModule,
      HttpModule,
      FormsModule,
      BrowserAnimationsModule,
      ToastrModule.forRoot(),
      InputTextModule, DataTableModule, ButtonModule, DialogModule,
      RouterModule.forRoot([
          { path: '', redirectTo: 'contact', pathMatch: 'full' },
          { path: 'contact', component: ContactComponent },
          { path: '**', redirectTo: 'contact' }
      ])
  ],
    providers: [
        ContactService,
        { provide: LocationStrategy, useClass: HashLocationStrategy },
        { provide: RequestOptions, useClass: AppBaseRequestOptions }
    ],
    bootstrap: [
        //AppComponent,
        ContactComponent
    ]
})
export class AppModule { }
